import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState({
      userPageToggle: (state) => state.userPageToggle,
    }),
    production() {
      return this.$store.getters.production_class;
    },
    collection() {
      return this.$store.getters.collection_class;
    },
  },
  methods: {
    select(data) {
      this.$store.commit("userPage_shift", data);
    },
  },
};
